import React from "react";
import {Box, Slider} from "@mui/material";
import {ModernSlider} from "./ModernSlider";


interface IProps {

}


interface IState {
    minFights: number,
    value: number,
    filterMinFights: number,
    filterMaxFights: number
}


export class TestComponent extends React.Component<IProps, IState> {
    constructor(props:any) {
        super(props);
        this.state={
            minFights: 5,
            value: 5,
            filterMinFights: 0,
            filterMaxFights: 100
        }
        this.changeMinFights=this.changeMinFights.bind(this)
        this.renderSliderText=this.renderSliderText.bind(this)
        this.changeValue=this.changeValue.bind(this)
    }
    changeMinFights(e:any,value: number | number [],activeThumb:any) {
        if (!Array.isArray(value)) {
            this.setState({minFights: value})
        }
    }
    renderSliderText(value:number,index:number) {
        return ""+value
    }
    changeValue(value:number) {
        this.setState({value:value})
    }
    render() {
        return (<Box className={"modernSlider"}>
            <Slider
                getAriaLabel={() => 'Minimum'}
                value={this.state.minFights}
                min={this.state.filterMinFights}
                max={this.state.filterMaxFights}
                onChange={this.changeMinFights}
                getAriaValueText={this.renderSliderText}
                color={"secondary"}
                track="inverted"
                marks
                step={1}
                valueLabelDisplay="on"
                disableSwap
            />
            <ModernSlider min={this.state.filterMinFights} max={this.state.filterMaxFights} callback={this.changeValue} label={"some"}/>
        </Box>)
    }
}