import {IHeroPack} from "../types/IHeroPack";
import React from "react";
import {Chip, Container, Divider, ToggleButton, ToggleButtonGroup} from "@mui/material";
import {HeroCounterHeroGrid, HeroCounterPackGrid} from "./index";
import {HeroData} from "../types";
import HeroRouletteDefendersMode from "./heroRouletteDefendersMode";

interface IProps {
    currentMode: number,
    heroesFilter: number[]
}

interface IState {
    currentMode: number,
    heroesFilter: number[],
    requestsInProgress: number,
    heroIds: IHeroPack[],
    heroProbs: Map<number,number>
}

export class HeroBestPackMode extends React.Component<IProps, IState> {
    constructor(props:any) {
        super(props);
        const heroProbs=new Map<number,number>()
        this.state={
            currentMode: props.currentMode,
            heroesFilter: props.heroesFilter,
            requestsInProgress: 0,
            heroIds: [],
            heroProbs: heroProbs
        }
        this.setMode=this.setMode.bind(this)
        this.filterHero=this.filterHero.bind(this)
        for (const x of HeroData.allHeroesIds) {
            this.state.heroProbs.set(x,-1)
        }
        this.updateHeroes(props.heroesFilter,props.currentMode,heroProbs)
    }
    requestSeq: number =0
    requestsInProgress: number =0

    updateHeroes(heroFilter:number[],currentMode:number,heroProbs:Map<number,number>) {
        const requestId=this.requestSeq++
        const heroParams=[-1,-1,-1,-1,-1]
        for (let i=0;i<5 && i<heroFilter.length;i++) {heroParams[i]=heroFilter[i];}
        this.requestsInProgress++
        this.setState({requestsInProgress:this.requestsInProgress})
        let url="https://g92e0914be8be53-db202112141238.adb.eu-frankfurt-1.oraclecloudapps.com/ords/hwstat2/hws/topPacks/"
        let gMode='a'
        switch (currentMode) {
            case 0: {gMode='a';break;}
            case 1: {gMode='d';break;}
            case 8010: {gMode='g8010';break;}
            case 8011: {gMode='g8011';break;}
            case 8012: {gMode='g8012';break;}
            case 8013: {gMode='g8013';break;}
            case 8014: {gMode='g8014';break;}
            case 8015: {gMode='g8015';break;}
        }

        fetch(url+gMode+"/0/0/0/"+heroParams.join("/"), {
            method: "GET"
        }).then(res => res.json())
            .then(
                (result) => {
                    if (result['items']) {
                        const heroLostPacks = []
                        let cnt = 0
                        for (const x of Array.from(heroProbs.keys())) {
                            heroProbs.set(x,-1)
                        }
                        const heroUniqMap=new Map<string,number>()
                        for (const x in result['items']) {
                            const z = result['items'][x]
                            let lostPack = [~~z.d1, ~~z.d2, ~~z.d3, ~~z.d4, ~~z.d5]
                            //console.log('LPU '+lostPack.join(',')+" wr="+(z['avgwinrate']*100).toFixed(1)+" lr="+(z['avglossrate']*100).toFixed(1))

                            for (const heroId of lostPack) {
                                let m = heroProbs.get(~~heroId)
                                if (m && m>=0) {
                                    m++
                                } else {
                                    m = 1
                                }
                                heroProbs.set(~~heroId, m)
                            }
                            cnt++
                            let extText=""
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"sumwinrate","Рейтинг_побед_в_роли_атакующего",100,"%",1)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"avgwinrate","Процент_побед_в_роли_атакующего",100,"%",1)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"cntwinpacks","Всего_пачек_защищалось_от_этого_пака",1,"",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"winsa","Выиграно_боев_в_роли_атакующего",1,"",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"cntfightsa","Всего_боев_в_роли_атакующего",1,"",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"sumlossrate","Рейтинг_побед_в_роли_защищающегося",100,"%",1)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"avglossrate","Процент_побед_в_роли_защищающегося",100,"%",1)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"cntlosspacks","Всего_пачек_атаковало_этого_пак",1,"",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"winsd","Выиграно_боев_в_роли_защищаегося",1,"",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"cntfightsd","Всего_боев_в_роли_защищаегося",1,"",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"h0d","Урон_гидре_огня",1/1000,"k",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"h1d","Урон_гидре_тьмы",1/1000,"k",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"h2d","Урон_гидре_света",1/1000,"k",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"h3d","Урон_гидре_земли",1/1000,"k",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"h4d","Урон_гидре_воды",1/1000,"k",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"h5d","Урон_гидре_воздуха",1/1000,"k",0)
                            //lostPack.push(Number.parseFloat(z.r))
                            //console.log('XXX5',z)
                            if (!heroUniqMap.get(lostPack.join(','))) {
                                heroLostPacks.push({hIds:lostPack,extendedInfo:{statusInfo:extText,wra:z.avgwinrate*100,wrd:z.avglossrate*100,ca:z.cntfightsa,cd:z.cntfightsd}})
                                heroUniqMap.set(lostPack.join(','),1)
                            }

                            //console.log('HLP',heroLostPacks)
                        }
                        for (const heroPackId of Array.from(heroProbs.keys())) {
                            const mmd = heroProbs.get(~~heroPackId)
                            if (mmd && cnt > 0) heroProbs.set(~~heroPackId, 100 * mmd / cnt)
                        }
                            this.setState(
                                {heroIds: heroLostPacks, heroProbs: heroProbs}
                            )

                    }
                }).finally(()=>{
            this.requestsInProgress--
            this.setState({requestsInProgress:this.requestsInProgress})
        })
    }

    filterHero(heroId: number) {
        if (HeroData.allHeroesIds.includes(heroId)) {
            const heroesFilter = this.state.heroesFilter
            const index = heroesFilter.indexOf(heroId, 0)
            if (index > -1) {
                heroesFilter.splice(index, 1);
                this.setState({heroesFilter: heroesFilter})
                this.updateHeroes(heroesFilter, this.state.currentMode, this.state.heroProbs)
            } else {
                if (heroesFilter.length < 5) {
                    heroesFilter.push(heroId)
                    this.setState({heroesFilter: heroesFilter})
                    this.updateHeroes(heroesFilter, this.state.currentMode, this.state.heroProbs)
                }
            }
        }
    }

    setMode(e:any,value:string) {
        const currentMode=~~value
        const heroFilter:number[] = []
        this.setState({currentMode:currentMode,heroesFilter:heroFilter})
        this.updateHeroes(heroFilter,currentMode,this.state.heroProbs)
    }

    render() {
        return (<Container sx={{
            bgcolor: 'background.default',
            display: "flex",
            flexDirection: "column",
            justifyContent: "flex-start",
            alignItems: "center",
        }}>
            <ToggleButtonGroup
                color="secondary"
                value={""+this.state.currentMode}
                exclusive
                onChange={this.setMode}
            >
                <ToggleButton value="0" color="primary">Лучшие паки в атаке</ToggleButton>
                <ToggleButton value="1" color="primary">Лучшие паки в защите</ToggleButton>
                <ToggleButton value="8010" color="primary">Гидра Огня</ToggleButton>
                <ToggleButton value="8011" color="primary">Гидра Тьмы</ToggleButton>
                <ToggleButton value="8012" color="primary">Гидра Света</ToggleButton>
                <ToggleButton value="8013" color="primary">Гидра Земли</ToggleButton>
                <ToggleButton value="8014" color="primary">Гидра Воды</ToggleButton>
                <ToggleButton value="8015" color="primary">Гидра Воздуха</ToggleButton>
            </ToggleButtonGroup>
            <Divider />
            <Divider><Chip label={this.state.requestsInProgress === 0?"Фильтр по героям":"Идет загрузка...просто жди!("+this.state.requestsInProgress+")"}/></Divider>
            <HeroCounterHeroGrid heroProbs={this.state.heroProbs}
                                 onFilterCounterHero={this.filterHero}
                                 heroCounterFilter={this.state.heroesFilter} key={10+this.state.currentMode+this.requestSeq}/>
            <Divider><Chip label={this.state.requestsInProgress === 0?"Лучшие паки":"Идет загрузка...просто жди! ("+this.state.requestsInProgress+")"}/></Divider>
            <HeroCounterPackGrid heroLostPacks={this.state.heroIds}
                                 onFilterCounterHero={this.filterHero}
                                 heroCounterFilter={[]} key={20+this.state.currentMode+this.requestSeq} emptyMessage={"Не переживай. Этот пак не набрал 10 побед в "+(this.state.currentMode===0?"атаке":"защите")+". Попробуй добавить его в "+(this.state.currentMode===0?"атакующие":"защищающиеся")+" режиме рулетки, и если он победит, то тут появится результат."}/>
        </Container>)
    }

}