import React from "react";
import {Box} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";

interface IProps {
    message: string,
    openMenuFunction:any
}

interface IState {

}

export class HeaderBlock extends React.Component<IProps, IState> {
    constructor(props:any) {
        super(props);
        this.state = { }
    }
    render() {
        return (
            <Box className={"headerBlock"}>
                <MenuIcon onClick={this.props.openMenuFunction}/>
                <Box>{this.props.message}</Box>
            </Box>
        )
    }
}