import React from "react";
import {HeroIcon,HeroPackSelected} from "./index";
import {Box, Grid, Stack} from "@mui/material";
import {HeroData} from "../types";
import {IHeroPack} from "../types/IHeroPack";

interface IProps {
    onFilterCounterHero: any,
    heroCounterFilter: number[],
    mainHeroPack?: IHeroPack,
    mainHeroPackType?: string,
    heroLostPacks: IHeroPack[],
    emptyMessage: string,
}

interface IState {

}

export class HeroCounterPackGrid extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {};
    }

    renderHeroPacks() {
        const items = []
        let cnt=0
        for (const j in this.props.heroLostPacks) {

            const heroPackFull=this.props.heroLostPacks[j]
            const heroPack=heroPackFull.hIds
            if (this.props.heroCounterFilter.indexOf(heroPack[0])<0 && this.props.heroCounterFilter.indexOf(heroPack[1])<0 && this.props.heroCounterFilter.indexOf(heroPack[2])<0 && this.props.heroCounterFilter.indexOf(heroPack[3])<0 &&this.props.heroCounterFilter.indexOf(heroPack[4])<0)
                items.push(<HeroPackSelected  key={heroPack.join(',')+this.props.mainHeroPack?.hIds?.join(',')+this.props.mainHeroPackType} heroPack={heroPackFull} heroIdsA={this.props.mainHeroPack?(this.props.mainHeroPackType==='a'?this.props.mainHeroPack:heroPackFull):undefined } heroIdsD={this.props.mainHeroPack?(this.props.mainHeroPackType==='d'?this.props.mainHeroPack:heroPackFull):undefined } deselectHero={this.props.onFilterCounterHero}/>)
        }
        if (items.length===0) {
            items.push(
                <Stack direction="row" key={cnt++} sx={{
                    bgcolor: 'primary.light',
                    m:0.25
                }}>{this.props.emptyMessage}</Stack>
            )
        }
        return items
    }

    render() {
        return (<Stack className={"heroPackStack"} sx={{
            margin: 1,
            bgcolor: 'primary.dark',

        }}>
            {this.renderHeroPacks()}
        </Stack>)
    }
}

export default HeroCounterPackGrid