import {IHeroPack} from "../types/IHeroPack";
import React, {useEffect, useState} from "react";
import {Box, Button, Chip, Divider} from "@mui/material";
import HourglassBottomIcon from '@mui/icons-material/HourglassBottom';
import {HeroData} from "../types";
import {SelectedHeroPack} from "./SelectedHeroPack";
import FilterAltIcon from '@mui/icons-material/FilterAlt';
import DeleteIcon from '@mui/icons-material/Delete';
import {CounterPackFilterModal} from "./CounterPackFilterModal";
import {MySettings} from "../types/MySettings";

function constructHeroFilter(pHeroFilter: number[]) {
    let result = "%"
    pHeroFilter.sort((a,b) => {
        return a-b
    })
    for (let i = 0; i <= 5 && i < pHeroFilter.length; i++) {
        result += ':' + pHeroFilter[i] + ':%'
    }
    return escape(result)
}

export function CounterPackResults(props: any) {
    let pMinFights = 0;
    if (props.params.get("minFightsCount")) {
        const x = ~~props.params.get("minFightsCount")
        if (x >= 0) {
            pMinFights = x
        }
    }
    const pHeroFilterInclude = []
    if (props.params.get("heroFilterInclude")) {
        const x = props.params.get("heroFilterInclude")?.split(",") || []
        for (let i = 0; i < 5 && i < x.length; i++) {
            if (HeroData.getHeroById(~~x[i])) {
                pHeroFilterInclude.push(~~x[i])
            }
        }
    }
    const pHeroFilterExclude = []
    if (props.params.get("heroFilterExclude")) {
        const x = props.params.get("heroFilterExclude")?.split(",") || []
        for (let i = 0; i < 5 && i < x.length; i++) {
            if (HeroData.getHeroById(~~x[i])) {
                pHeroFilterExclude.push(~~x[i])
            }
        }
    }
    let pMinWinRate = 0
    if (props.params.get("minWinRate")) {
        const x = ~~props.params.get("minWinRate")
        if (x >= 0 && x <= 100) {
            pMinWinRate = x
        }
    }
    const [minWinRate, setMinWinRate] = useState(pMinWinRate)
    const [minFights, setMinFights] = useState(pMinFights)
    const [heroFilterInclude, setHeroFilterInclude] = useState(pHeroFilterInclude)
    const [heroFilterExclude, setHeroFilterExclude] = useState(pHeroFilterExclude)
    const [heroPacks, setHeroPacks] = useState([] as IHeroPack[])
    const [requestsInProgress, setRequestsInProgress] = useState(0)
    const [, setTotalFound] = useState(0)
    const [filterMinWinRate, setFilterMinWinRate] = useState(0)
    const [filterMaxWinRate, setFilterMaxWinRate] = useState(0)
    const [filterMinFights, setFilterMinFights] = useState(0)
    const [filterMaxFights, setFilterMaxFights] = useState(0)
    const [filterOpened, setFilterOpened] = useState(false)
    const [isInitialized, setIsInitialized] = useState(false)
    const [isUpdateOccurred, setIsUpdateOccurred] = useState(false)
    const [memoArray, setMemoArray] = useState({
        minWinRate: minWinRate,
        minFights: minFights,
        heroFilterInclude: heroFilterInclude,
        heroFilterExclude: heroFilterExclude,
        filterMinFights: filterMinFights,
        filterMaxFights: filterMaxFights,
        filterMinWinRate: filterMinWinRate,
        filterMaxWinRate: filterMaxWinRate,
        requestsInProgress: 0
    })
    const updateHeroPackInfo = function (pHeroPack: IHeroPack, pMinWinRate: number, pMinFights: number, pHeroFilterInclude: number[], pHeroFilterExclude: number[]) {
        if (pHeroPack.hIds.length === 5) {
            let url = MySettings.apiUrl+"lostPacks/"
            const heroParams = [-1, -1, -1, -1, -1]
            for (let i = 0; i < 5 && i < pHeroPack.hIds.length; i++) {
                heroParams[i] = pHeroPack.hIds[i];
            }
            props.changeHeaderMessageFunction("Загружаем контр-паки из статистики...")
            url += heroParams.join("/")
            url += "/" + pMinWinRate + "/" + pMinFights + "/"
            url += constructHeroFilter(pHeroFilterInclude) + "/"
            url += constructHeroFilter(pHeroFilterExclude.length === 0 ? [-2] : pHeroFilterExclude)
            memoArray.requestsInProgress++
            setRequestsInProgress(memoArray.requestsInProgress)
            fetch(url, {
                method: "GET"
            }).then(res => res.json())
                .then(
                    (result) => {
                        let nTotalFound = 0
                        if (result['items']) {
                            const heroLostPacks = []
                            let nFilterMinWinRate = 0
                            let nFilterMaxWinRate = 0
                            let nFilterMinFights = 0
                            let nFilterMaxFights = 0

                            for (const x in result['items']) {
                                const z = result['items'][x]
                                let lostPack = []
                                lostPack = [~~z.d1, ~~z.d2, ~~z.d3, ~~z.d4, ~~z.d5]
                                if (!isUpdateOccurred) {
                                    nFilterMinWinRate = z.minwr
                                    nFilterMaxWinRate = z.maxwr
                                    nFilterMinFights = z.minc
                                    nFilterMaxFights = z.maxc
                                }
                                nTotalFound = z.tc
                                heroLostPacks.push({
                                    hIds: lostPack,
                                    extendedInfo: {
                                        statusInfo: "",
                                        wra: z.wra,
                                        wrd: z.wrd,
                                        ca: z.ca,
                                        cd: z.cd,
                                        wa: z.wa,
                                        wd: z.wd,
                                        cc: z.cc,
                                        cwr: z.cwr
                                    }
                                })
                            }

                            setHeroPacks(heroLostPacks)
                            if (!isUpdateOccurred) {
                                setFilterMinWinRate(Math.floor(nFilterMinWinRate))
                                setFilterMaxWinRate(Math.floor(nFilterMaxWinRate))
                                setFilterMinFights(nFilterMinFights)
                                setFilterMaxFights(nFilterMaxFights)
                            }
                            setTotalFound(nTotalFound)

                        }
                        if (!isUpdateOccurred) setIsUpdateOccurred(true)
                        props.changeHeaderMessageFunction("Нашли " + nTotalFound + " контр-паков")
                    }).finally(() => {
                memoArray.requestsInProgress--
                setRequestsInProgress(memoArray.requestsInProgress)
            })
        }

    }
    const onHeroSelect = function () {
    }
    const onFilterClick = function () {
        setMemoArray(
            {
                minWinRate: minWinRate,
                minFights: minFights,
                heroFilterInclude: heroFilterInclude,
                heroFilterExclude: heroFilterExclude,
                filterMinFights: filterMinFights,
                filterMaxFights: filterMaxFights,
                filterMinWinRate: filterMinWinRate,
                filterMaxWinRate: filterMaxWinRate,
                requestsInProgress: memoArray.requestsInProgress
            }
        )
        setFilterOpened(true)
    }
    const handleModalClose = function (newMemoArray: any) {
        if (newMemoArray.minWinRate === filterMinWinRate) {
            newMemoArray.minWinRate = 0
        }
        setMinWinRate(newMemoArray.minWinRate)
        if (newMemoArray.minFights === filterMinFights) {
            newMemoArray.minFights = 0
        }
        setMinFights(newMemoArray.minFights)
        const newHeroFilterInclude = [...newMemoArray.heroFilterInclude]
        const newHeroFilterExclude = [...newMemoArray.heroFilterExclude]
        setHeroFilterInclude(newHeroFilterInclude)
        setHeroFilterExclude(newHeroFilterExclude)
        setMemoArray(newMemoArray)
        setFilterOpened(false)
        updateHeroPackInfo(props.heroPack, newMemoArray.minWinRate, newMemoArray.minFights, newHeroFilterInclude, newHeroFilterExclude)
    }
    const handleDeleteFilterChip = function (e: any) {
        let filterType = undefined
        let heroId = undefined
        if (e?.target?.parentNode?.parentNode?.dataset?.filtertype) {
            filterType = e?.target?.parentNode?.parentNode?.dataset?.filtertype
            heroId = ~~e?.target?.parentNode?.parentNode?.dataset?.filterheroid
        } else if (e?.target?.parentNode?.dataset?.filtertype) {
            filterType = e?.target?.parentNode?.dataset?.filtertype
            heroId = ~~e?.target?.parentNode?.dataset?.filterheroid
        }
        if (filterType && heroId) {
            switch (filterType) {
                case "include": {
                    const index = heroFilterInclude.indexOf(heroId)
                    if (index >= 0) {
                        const newHeroFilterInclude = [...heroFilterInclude]
                        newHeroFilterInclude.splice(index, 1)
                        setHeroFilterInclude(newHeroFilterInclude)
                        updateHeroPackInfo(props.heroPack, minWinRate, minFights, newHeroFilterInclude, heroFilterExclude)
                    }
                    break;
                }
                case "exclude": {
                    const index = heroFilterExclude.indexOf(heroId)
                    if (index >= 0) {
                        const newHeroFilterExclude = [...heroFilterExclude]
                        newHeroFilterExclude.splice(index, 1)
                        setHeroFilterExclude(newHeroFilterExclude)
                        updateHeroPackInfo(props.heroPack, minWinRate, minFights, heroFilterInclude, newHeroFilterExclude)
                    }
                    break;
                }
                case "mwr": {
                    setMinWinRate(0)
                    updateHeroPackInfo(props.heroPack, 0, minFights, heroFilterInclude, heroFilterExclude)
                    break;
                }
                case "mf": {
                    setMinFights(0)
                    updateHeroPackInfo(props.heroPack, minWinRate, 0, heroFilterInclude, heroFilterExclude)
                    break;
                }
            }
        }
    }
    const renderFilterBar = function () {
        const items = []
        for (const x of heroFilterInclude) {
            items.push(<Chip
                data-filtertype={"include"}
                data-filterheroid={x}
                label={"+" + HeroData.getHeroById(x)?.heroName || "" + x}
                onClick={onFilterClick}
                onDelete={handleDeleteFilterChip}
                deleteIcon={<DeleteIcon/>}
                variant="outlined"
            />)
        }
        for (const x of heroFilterExclude) {
            items.push(<Chip
                data-filtertype={"exclude"}
                data-filterheroid={x}
                label={"-" + HeroData.getHeroById(x)?.heroName || "" + x}
                onClick={onFilterClick}
                onDelete={handleDeleteFilterChip}
                deleteIcon={<DeleteIcon/>}
                variant="outlined"
            />)
        }
        if (minFights > 0) {
            items.push(<Chip
                data-filtertype={"mf"}
                data-filterheroid={1}
                label={<Box className={"filterBarLabel"}><img src={"./attack_w.png"}/><span>{minFights}</span></Box>}
                onClick={onFilterClick}
                onDelete={handleDeleteFilterChip}
                deleteIcon={<DeleteIcon/>}
                variant="outlined"
            />)
        }
        if (minWinRate > 0) {
            items.push(<Chip
                data-filtertype={"mwr"}
                data-filterheroid={1}
                label={<Box className={"filterBarLabel"}><img src={"./trophy_a_w.png"}/><span>{minWinRate + "%"}</span></Box>}
                onClick={onFilterClick}
                onDelete={handleDeleteFilterChip}
                deleteIcon={<DeleteIcon/>}
                variant="outlined"
            />)
        }

        return (<Box className={"filterBar"}>
            {items}
        </Box>)
    }
    const renderHeroPacks = function () {
        const items = []
        const emptyMap = new Map<number, number>()
        for (const i in heroPacks) {
            items.push(<SelectedHeroPack additionalClasses={["withCurrentStat"]} heroPack={heroPacks[i]}
                                         heroProbs={emptyMap}
                                         onHeroSelect={onHeroSelect} selectedHeroes={heroFilterInclude}
                                         key={heroPacks[i].hIds.join(",")}/>)
        }
        return items
    }
    useEffect(() => {
        if (!isInitialized) {
            updateHeroPackInfo(props.heroPack, minWinRate, minFights, heroFilterInclude, heroFilterExclude)
            setIsInitialized(true)
        }
    })

    return (
        <Box className={"counterPackResult"}>
            {requestsInProgress > 0 && (
                <Box>Loading...<HourglassBottomIcon/></Box>
            )
            }
            {isUpdateOccurred && requestsInProgress === 0 && (
                <Box>
                    <Box><Divider>Фильтр<Button className={"svgButton"}><FilterAltIcon
                        onClick={onFilterClick}/></Button></Divider>
                        {filterOpened && (
                            <CounterPackFilterModal onClose={handleModalClose} memoArray={memoArray}/>
                        )}
                        {renderFilterBar()}
                    </Box>
                    {heroPacks.length === 0 && (<Box className={"counterPackNotFound"}>
                        <Box>Не нашли контр-паки у себя в статистике боев</Box>
                        <span>Попробовать добавить в очередь на поиск на сервер обработки боёв?</span>
                        <Button sx={{
                            color: 'white',
                            bgcolor: 'primary.light'
                        }}>Найти контр-пак (пока не работает)</Button>
                    </Box>)}
                    {heroPacks.length > 0 && (<Box> <Divider>Контр-паки</Divider>
                            <Box className={"counterPackResultPacks"}>
                                {renderHeroPacks()}
                            </Box>
                        </Box>
                    )}
                </Box>)}
        </Box>
    )
}