import React from "react";
import {HeroIcon} from "./index";
import {Grid} from "@mui/material";
import {HeroData} from "../types";
import {IHeroPack} from "../types/IHeroPack";
import {HeroModernIcon} from "../components2/HeroModernIcon";

interface IProps {
    heroIds: IHeroPack,
    selectHero: any,
    heroProbs?: Map<number,number>,
    showAll: boolean
}

interface IState {
    heroIds: IHeroPack
}

export class HeroGrid extends React.Component<IProps, IState>{
    constructor(props:any) {
        super(props);
        this.state = {
            heroIds: props.heroIds
        };
    }

    renderHeroes() {
        var heroProb: number | undefined = 0;
        const items = []
        const heroArray:any[]=[]
        for (const value of Array.from(HeroData.heroesList.values())) {
            //console.log('vvv',x,hers,value)
            heroProb=this.props.heroProbs?this.props.heroProbs.get(value.heroId):0
            heroProb=heroProb !== undefined?heroProb:0
            if (this.props.showAll || heroProb>0) {
                heroArray.push({heroId:value.heroId,heroProb:heroProb,heroName:value.heroName})
            }
        }

        if (heroArray.length==0) {
            for (const value of Array.from(HeroData.heroesList.values())) {
                    heroArray.push({heroId:value.heroId,heroProb:0,heroName:value.heroName})
            }
        }
        heroArray.sort((a,b)=> {
            return a.heroName>b.heroName?1:-1
        })

        for (const i in heroArray) {
            const x=heroArray[i]
            //console.log("GRID: ",i,x.heroId,x.heroProb)
            //items.push(<HeroIcon heroId={x.heroId} renderType={0} isSelected={this.state.heroIds.hIds.includes(x.heroId)} OnClick={this.props.selectHero} heroProb={x.heroProb} key={x.heroId}/>)
            const additionalClasses=["heroIconSmall"]
            if (this.state.heroIds.hIds.includes(x.heroId)) {additionalClasses.push("mySelected")}
            items.push(<HeroModernIcon heroId={x.heroId} additionalClasses={additionalClasses} onClick={this.props.selectHero} heroProb={x.heroProb} key={x.heroId}/>)
        }
        return items
    }

    render() {

        return (<Grid container spacing={0} className={"heroGrid"} sx={{
            m: 0,
            justifyContent: 'center'
        }} >
            {this.renderHeroes()}
        </Grid>)
    }
}
export default HeroGrid