import React from "react";
import {HeroGrid, HeroPackSelected} from "./index";
import {Box, Button, Chip, Container, Divider} from "@mui/material";
import {HeroData} from "../types";
import {IHeroPack} from "../types/IHeroPack";
import {MyNavigator} from "../types/MyNavigator";
import {MySettings} from "../types/MySettings";

interface IQueueItem {
    heroIdsA: IHeroPack,
    heroIdsD: IHeroPack
}

interface IProps {
    heroIdsA: IHeroPack,
    heroIdsD: IHeroPack,
    navigateFunction: any
}

interface IState {
    heroIdsA: IHeroPack,
    heroIdsD: IHeroPack,
    displayEditA: boolean,
    displayEditD: boolean,
    requestsInProgress: number
}

export class HeroAddToTrainMode extends React.Component<IProps, IState> {
    static emptyMap=new Map<number,number>()
    constructor(props:any) {
        super(props);
        this.state = {
            heroIdsA: props.heroIdsA,
            heroIdsD: props.heroIdsD,
            displayEditA: props.heroIdsA.hIds.length!==5,
            displayEditD: props.heroIdsD.hIds.length!==5,
            requestsInProgress: 0
        };
        console.log('HeroAddToTrainMode created',this.state.heroIdsA,this.state.heroIdsD)
        this.selectHeroA=this.selectHeroA.bind(this)
        this.selectHeroD=this.selectHeroD.bind(this)
        this.toggleEditA=this.toggleEditA.bind(this)
        this.toggleEditD=this.toggleEditD.bind(this)
        this.sendToQueue=this.sendToQueue.bind(this)
        this.setAttackHeroPack=this.setAttackHeroPack.bind(this)
        this.setDefenderHeroPack=this.setDefenderHeroPack.bind(this)
        MyNavigator.heroAddToTrainMode=this
    }

    sendToQueue(e:any) {
        const body:any={
            a1:-1,
            a2:-1,
            a3:-1,
            a4:-1,
            a5:-1,
            d1:-1,
            d2:-1,
            d3:-1,
            d4:-1,
            d5:-1
        }
        for (let i=0;i<5 && i<this.state.heroIdsA.hIds.length;i++) {body["a"+(i+1)]=this.state.heroIdsA.hIds[i];}
        for (let i=0;i<5 && i<this.state.heroIdsA.hIds.length;i++) {body["d"+(i+1)]=this.state.heroIdsD.hIds[i];}
        const state=this.state; this.setState({requestsInProgress:state.requestsInProgress+1})
        fetch(MySettings.apiUrl+"queue/",{
            method: "POST",
            headers: {
                "Content-type": "application/json"
            },
            body: JSON.stringify(body)}).then(()=>{
                console.log("Good")
                this.props.navigateFunction(2)
        }).finally(()=>{
            const state=this.state; this.setState({requestsInProgress:state.requestsInProgress-1})
        })
    }

    setAttackHeroPack(currentPack: IHeroPack) {
        this.setState({heroIdsA:currentPack})
    }

    setDefenderHeroPack(currentDefenderPack: IHeroPack) {
        this.setState({heroIdsD:currentDefenderPack})
    }

    selectHeroA(heroId: number) {
        if (HeroData.allHeroesIds.includes(heroId)) {
            const heroPack = this.state.heroIdsA
            const index = heroPack.hIds.indexOf(heroId)
            if (index < 0) {
                if (heroPack.hIds.length < 5) {
                    heroPack.hIds.push(heroId)
                    heroPack.extendedInfo = {statusInfo: ""}
                    this.setState({heroIdsA:heroPack })
                }
            } else {
                heroPack.extendedInfo = {statusInfo: ""}
                heroPack.hIds.splice(index, 1);
                this.setState({heroIdsA:heroPack })
            }
        }
    }
    selectHeroD(heroId: number) {
        if (HeroData.allHeroesIds.includes(heroId)) {
            const heroPack = this.state.heroIdsD
            const index = heroPack.hIds.indexOf(heroId)
            if (index < 0) {
                if (heroPack.hIds.length < 5) {
                    heroPack.hIds.push(heroId)
                    heroPack.extendedInfo = {statusInfo: ""}
                    this.setState({heroIdsD:heroPack })
                }
            } else {
                heroPack.extendedInfo = {statusInfo: ""}
                heroPack.hIds.splice(index, 1);
                this.setState({heroIdsD:heroPack })
            }
        }
    }



    toggleEditA(e:any) {
        const newState=!this.state.displayEditA
        this.setState({displayEditA:newState})
    }

    toggleEditD(e:any) {
        const newState=!this.state.displayEditD
        this.setState({displayEditD:newState})
    }

    renderA() {
        if (this.state.displayEditA) {
            return (<Box className={"heroADBox"}><Divider><Chip label={this.state.requestsInProgress>0?"Идет загрузка...просто жди!":"Атакующий пак"} /></Divider>
                    <HeroPackSelected heroPack={this.state.heroIdsA} deselectHero={this.selectHeroA}/>
                    {this.state.heroIdsA.hIds.length>0?
                        <Button variant="outlined"  className={"heroEditPackButton"} onClick={this.toggleEditA}>Готово</Button>
                        :
                        null}
                <Divider><Chip
                        label={this.state.requestsInProgress > 0 ? "Идет загрузка...просто жди!" : "Выбери атакующих героев"}/></Divider>
                    <HeroGrid showAll={false} heroIds={this.state.heroIdsA} heroProbs={HeroAddToTrainMode.emptyMap}
                              selectHero={this.selectHeroA}/>
            </Box>)
        } else {
            return (<Box className={"heroADBox"}><Divider><Chip label={this.state.requestsInProgress>0?"Идет загрузка...Просто жди!":"Атакующий пак"} /></Divider>

                    <HeroPackSelected heroPack={this.state.heroIdsA}  deselectHero={this.selectHeroA}/>
                        <Button variant="outlined"  className={"heroEditPackButton"} onClick={this.toggleEditA}>Изменить</Button>
            </Box>)
        }
    }



    renderB() {
        if (this.state.displayEditD) {
            return (<Box  className={"heroADBox"}>
                <Divider><Chip label={this.state.requestsInProgress>0?"Идет загрузка...Просто жди!":"Защищающийся пак"} /></Divider>
                    <HeroPackSelected heroPack={this.state.heroIdsD} deselectHero={this.selectHeroD}/>
                    {this.state.heroIdsD.hIds.length>0?
                        <Button variant="outlined" className={"heroEditPackButton"} onClick={this.toggleEditD}>Готово</Button>
                        :
                        null}
                <Divider><Chip label={this.state.requestsInProgress>0?"Идет загрузка...Просто жди!":"Выбери защищающихся героев"} /></Divider>
                <HeroGrid showAll={false} heroIds={this.state.heroIdsD} heroProbs={HeroAddToTrainMode.emptyMap}
                          selectHero={this.selectHeroD}/>
            </Box>)
        } else {
            return (<Box  className={"heroADBox"}>
                <Divider><Chip label={this.state.requestsInProgress>0?"Идет загрузка...Просто жди!":"Защищающийся пак"} /></Divider>
                    <HeroPackSelected heroPack={this.state.heroIdsD} deselectHero={this.selectHeroD}/>
                    <Button variant="outlined" className={"heroEditPackButton"} onClick={this.toggleEditD}>Изменить</Button>
            </Box>)
        }
    }
    renderSendButton() {
        if (this.state.heroIdsA.hIds.length>0 && this.state.heroIdsD.hIds.length>0 && !this.state.displayEditA && !this.state.displayEditD) {
            return (<Button variant="contained" onClick={this.sendToQueue} sx={{
                marginTop: '1em',
                maxWidth: 'min-content'
            }}>Отправить</Button>)
        } else {
            return (<Button disabled>Выбери героев перед отправкой</Button>)
        }
    }

    render() {
        console.log('RHATTM',this.state.heroIdsA,this.state.heroIdsD)
        return (<Box sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center'
        }}>
            {this.renderA()}
            {this.renderB()}
            {this.renderSendButton()}
        </Box>)
    }

}

export default HeroAddToTrainMode;