import React from "react";
import {Box, Slide, Tab, Tabs} from "@mui/material";
import {CounterPackScreen} from "./CounterPackScreen";
import {HashRouter, Navigate, Route, Routes} from "react-router-dom"
import {TestComponent} from "./TestComponent";
import GLogin from "./GLogin";
import GLogout from "./GLogout";

interface IProps {

}

interface IState {
    navMenuIndex: number,
    navMenuOpened: boolean,
}

export class MainScreen extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            navMenuIndex: 0,
            navMenuOpened: false,
        }
        this.changeMenu = this.changeMenu.bind(this)
        this.toggleMenu = this.toggleMenu.bind(this)
    }


    changeMenu(event: any, newValue: string) {
        this.setState({
            navMenuIndex: ~~newValue
        })
    }

    toggleMenu() {
        this.setState({
            navMenuOpened: !this.state.navMenuOpened
        })
    }

    render() {
        //{!this.state.navMenuOpened && (
        /*
                     <GLogin/>
                    <GLogout/>
         */
        return (
            <HashRouter>
                <Box className={"mainScreen"}
                     sx={{flexGrow: 1, display: 'flex', width: '100vw', height: '100%', overflowX: 'hidden'}}>

                    <Slide direction="right" in={this.state.navMenuOpened}>
                        <Box className={"menuBox"}>
                            <Tabs orientation="vertical"
                                  variant="scrollable"
                                  value={this.state.navMenuIndex}
                                  onChange={this.changeMenu}
                                  aria-label="Vertical tabs example"
                                  sx={{borderRight: 1, borderColor: 'divider'}}>
                                <Tab label="Item One" id={"vertical-tab-0"} aria-controls={"vertical-tabpanel-0"}/>
                                <Tab label="Item Two" id={"vertical-tab-1"} aria-controls={"vertical-tabpanel-1"}/>
                            </Tabs>
                            <Box className={"menuFadeBox"} onClick={this.toggleMenu}/>
                        </Box>
                    </Slide>
                    <Box role="tabpanel">
                        <Routes>
                            <Route path={"/counterPack"} element={
                                <CounterPackScreen openMenuFunction={this.toggleMenu}
                                                   />
                            }/>
                            <Route path={"/test"} element={
                                <TestComponent/>
                            }/>
                            <Route path={"*"} element={
                                <Navigate replace to="/counterPack" />}
                            />
                        </Routes>
                    </Box>
                </Box>
            </HashRouter>
        )
    }
}
