import React from "react";
import {Box, Menu, MenuItem, Tooltip} from "@mui/material";
import {IHeroPack} from "../types/IHeroPack";
import {HeroModernIcon} from "./HeroModernIcon";
import {HeroData} from "../types";
import MoreVertIcon from '@mui/icons-material/MoreVert';
import LinkIcon from '@mui/icons-material/Link';

interface IProps {
    heroPack: IHeroPack,
    heroProbs: Map<number, number>,
    onHeroSelect: any,
    selectedHeroes: number[],
    additionalClasses: string[],
    currentPack?: IHeroPack,
    currentInAttack?: boolean
}

interface IState {
    anchorEl: any,
}

export class SelectedHeroPack extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            anchorEl: null,
        }

        this.seeCounterPacks = this.seeCounterPacks.bind(this)
        this.handleMenuClick = this.handleMenuClick.bind(this)
        this.handleMenuClose = this.handleMenuClose.bind(this)
    }

    renderHeroIcons() {
        this.props.heroPack.hIds.sort((a, b) => {
            return (HeroData.getHeroById(a)?.battleOrder || 0) - (HeroData.getHeroById(b)?.battleOrder || 0)
        })
        const items: any = []
        for (let i = 0; i < 5; i++) {
            const hId = this.props.heroPack.hIds[i]
            items.push(<HeroModernIcon key={i} additionalClasses={[]} heroId={hId}
                                       heroProb={this.props.heroProbs.get(hId) || 0}
                                       onClick={this.props.onHeroSelect}/>)
        }
        return items
    }

    copyLink() {
        navigator.clipboard.writeText("this.state.textToCopy")
    }

    seeCounterPacks(e: any) {

    }

    handleMenuClick(e: any) {
        this.setState({anchorEl: e.currentTarget})
    }

    handleMenuClose(e: any) {
        this.setState({anchorEl: null})
    }

    renderMenuItems() {
        const menuItems :JSX.Element[] = []
        let key = 0
        menuItems.push(<MenuItem key={key++} onClick={this.seeCounterPacks}>Посмотреть кто побеждает (не работает)</MenuItem>)
        /*if (this.props.heroIdsA !== undefined && this.props.heroIdsD !== undefined) menuItems.push(<MenuItem key={key++} onClick={this.seeTrainMode}>Add to Train both packs</MenuItem>)*/
        return menuItems
    }

    render() {
        const additionalClasses = "selectedHeroPack " + this.props.additionalClasses.join(" ")
        return (
            <Box className={additionalClasses}>
                {this.props.heroPack?.extendedInfo?.cwr !== undefined && this.props.heroPack?.extendedInfo?.cc !== undefined &&
                (<Box className={"selectedHeroPackExtendedCurrent"}>
                    <Tooltip title={"Процент побед выбранного пака"}><span><img
                        src={"./trophy_a_w.png"}/><span>{this.props.heroPack.extendedInfo.cwr + "%"}</span></span></Tooltip>
                    <Tooltip title={"Количество побед выбранного пака"}><span><img
                        src={"./attack_w.png"}/><span>{this.props.heroPack.extendedInfo.cc}</span></span></Tooltip>
                </Box>)}
                <Box className={"selectedHeroPackIcons"}>
                    {this.renderHeroIcons()}
                    <Box className={"linkVerticalBar"}>
                        <Box id="demo-positioned-button"
                             aria-controls="demo-positioned-menu"
                             aria-haspopup="true"
                             aria-expanded={this.state.anchorEl ? 'true' : undefined}
                             onClick={this.handleMenuClick}><Tooltip title={"Дополнительная информация"}><MoreVertIcon/></Tooltip></Box>
                        <Tooltip title={"Копировать ссылку на пак"}><LinkIcon onClick={this.copyLink}/></Tooltip>
                    </Box>

                </Box>
                <Menu
                    id="demo-positioned-menu"
                    aria-labelledby="demo-positioned-button"
                    anchorEl={this.state.anchorEl}
                    open={!!this.state.anchorEl}
                    onClose={this.handleMenuClose}
                    anchorOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}
                    transformOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}
                >
                    {this.renderMenuItems()}
                </Menu>
                <Box className={"selectedHeroPackExtended"}>
                    <span>Всего:</span>
                    <Tooltip title={"Всего % побед в атаке"}><span><img
                        src={"./trophy_a_w.png"}/><span>{(this.props.heroPack?.extendedInfo?.wra === undefined ? "?" : this.props.heroPack?.extendedInfo?.wra) + "%"}</span></span></Tooltip>
                    <Tooltip title={"Всего побед в атаке"}><span><img
                        src={"./attack_w.png"}/><span>{this.props.heroPack?.extendedInfo?.ca === undefined ? "?" : this.props.heroPack?.extendedInfo?.ca}</span></span></Tooltip>
                    <Tooltip title={"Всего % побед в защите"}><span><img
                        src={"./trophy_d_w.png"}/><span>{(this.props.heroPack?.extendedInfo?.wrd === undefined ? "?" : this.props.heroPack?.extendedInfo?.wrd) + "%"}</span></span></Tooltip>
                    <Tooltip title={"Всего побед в защите"}><span><img
                        src={"./shield_w.png"}/><span>{this.props.heroPack?.extendedInfo?.cd === undefined ? "?" : this.props.heroPack?.extendedInfo?.cd}</span></span></Tooltip>
                </Box>
            </Box>
        )
    }
}