import React from "react";
import {HeroFightQueueMode} from "./heroFightQueueMode";
import {Box, Divider, ToggleButton, ToggleButtonGroup} from "@mui/material";
import HeroAddToTrainMode from "./heroAddToTrainMode";
import {MyNavigator} from "../types/MyNavigator";
import {IHeroPack} from "../types/IHeroPack";

interface IProps {
    currentMode: number,
    navigateToTopFunction: any
}

interface IState {
    currentMode: number,
}

export class HeroOneFightMode extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            currentMode: props.currentMode
        }
        this.setMode=this.setMode.bind(this)
        this.navigateToMode=this.navigateToMode.bind(this)

        MyNavigator.heroOneFightMode=this
    }
    navigateToMode(newMode:number) {
        this.setState({currentMode: newMode})
    }
    setMode(e:any) {
        if (e && e.target && e.target.dataset && e.target.dataset.mode) {
            this.setState({currentMode: ~~e.target.dataset.mode})
        }
    }
    renderMode(currentMode:number) {
        console.log('heroOneFightMode-renderMode',MyNavigator.currentPack,MyNavigator.currentDefenderPack)
        if (currentMode===1) {
            return (<HeroAddToTrainMode heroIdsA={MyNavigator.currentPack} heroIdsD={MyNavigator.currentDefenderPack} navigateFunction={this.navigateToMode}/>)
        } else if (currentMode===2) {
            return (<HeroFightQueueMode queueItems={[]}/>)
        } else {
            return (null)
        }
    }
    render() {
        return (
            <Box sx={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "flex-start",
                alignItems: "center",
            }}>
                <ToggleButtonGroup
                    color="secondary"
                    value={""+this.state.currentMode}
                    exclusive
                    onChange={this.setMode}
                >
                    <ToggleButton value="1" data-mode={1} color="primary">Добавить в очередь бой из 2х паков</ToggleButton>
                    <ToggleButton value="2" data-mode={2} color="primary">Очередь боев в обработке сервером</ToggleButton>
                </ToggleButtonGroup>
                <Divider />
                {this.renderMode(this.state.currentMode)}
            </Box>
        )
    }


}

export default HeroOneFightMode