import React from "react";
import {Box, Button, Chip, Container, Divider} from "@mui/material";

interface IProps {

}

interface IState {

}

export class HeroFAQ extends React.Component<IProps, IState> {
    constructor(props:any) {
        super(props);
        this.state={}
    }
    render() {
        return (<Box sx={{
            padding: '1em 1em 1em 1em'
        }}>
            <h3>1. Что это за сервис?</h3>
            <p>
                Статистика боёв на полной прокачке в мобильной версии Хроник Хаоса. Здесь можно не просто тестировать пачки, а даже тестировать их против Легендарной Гидры(new!)
            </p>
            <h3>2. Как им пользоваться?</h3>
            <p>
                Вверху меню - нажимаешь, смотришь ;-)
            </p>
            <h3>3. Ну и что там в меню "Найти кто или кого побеждает пак"?</h3>
            <p>
                Есть два режима - "Кто побеждает твой пак" и "Кого побеждает твой пак". Если нужно объяснять смысл этих названий, то сочувствую твоим друзьям и родителям и искренне тебе завидую.<br/>
                В этом режиме ты выбираешь из героев ниже пак. Список героев составлен только из тех, по кому есть статистика с победой в бою.<br/>
                Если твоего пака здесь нет - попробуй победить в защите или нападении в режимах "Одна битва" или "Рулетка".
            </p>
            <h3>4. А что в меню "Одна битва"?</h3>
            <p>
                Здесь выбираешь атакующий пак, защищающийся пак и отправляешь на обработку бой. Твой бой добавится в очередь<br/>
                Когда дайдет очередь до твоего боя (сервер бесплатный для тебя и не резиновый, делает по 3-5 боев в минуту)<br/>
                Когда сервер обработает бой, он пропадет из очереди и его результаты попадут в статистику, а если атакующий пак победит, ты сможешь найти результаты по атакующему или защищающемуся паку в меню "Найти кто или кого побеждает пак"
            </p>
            <h3>5. А что в меню "Рулетка"?</h3>
            <p>
                Здесь есть набор паков (48+ шт.), которые защищаются (подменю "Список защищающихся"), включая головы Легендарной гидры<br/>
                Ты можешь добавить свой пак в список защищающихся (подменю "Добавить в защищающиеся паки") или атакующих ("Добавить в атакующие паки")<br/>
                Если сервер простаивает (нет команд, добавленных шаловливыми ручками как у тебя), то генерится рандомный пак и он пробует атаковать весь список защитных паков этой рулетки<br/>
                Когда ты добавляешь свой пак в защищающиеся (подменю "Добавить в защищающиеся паки") в режиме рулетки, то он там висит только 6 часов (сервер нерезиновый, а таких как ты много)<br/>
                Когда ты добавляешь свой пак в атакующие ("Добавить в атакующие паки"), то сервер бросает всё (да щас!ха-ха!) и стравливает твой атакующий пак с защищающимися паками из списка в подменю "Список защищающихся".<br/>
            </p>
            <h3>6. Как формируется список в подменю "Список защищающихся" из режима "Рулетка"?</h3>
            <p>
                Есть четыре группы:<br/>
                1. Добавленные шаловливыми ручками такими как у тебя из меню ("Добавить в защищающиеся паки"). Они там держатся не больше 6 часов, потом умирают как и все живое<br/>
                2. Топ 15 атакующих паков по статистике на этом сервере<br/>
                3. Топ 15 защитных паков по статистике на этом сервере<br/>
                4. Паки админа и головы Легендарной гидры. Аз есмь админ добавил сюда паки на свое усмотрение и не собираюсь их обсуждать<br/>
            </p>
            <h3>7. Как можно протестировать свою пачку против гидры?</h3>
            <p>
                Только если добавить ее в атакующие паки в режиме "Рулетка".
            </p>
            <h3>8. Где найти лучшие пачки?</h3>
            <p>
                <s>У нас в клубе!</s> В меню "Топ паки".
            </p>
            <h3>9. Имеет ли этот сервис какое-то отношение к Nexters?</h3>
            <p>
                Конечно же нет!
            </p>
            <h3>10. Как связаться с админом?</h3>
            <p>
                Пока только в комментариях под видео на моем канале (нажмите на значок Youtube вверху).
            </p>
        </Box>)
    }
}