import React from "react";
import {Box} from "@mui/material";
import {HeroData} from "../types";
import {HeroModernIcon} from "./HeroModernIcon";

interface IProps {
    heroProb?: Map<number,number>,
    onClick: any,
    selectedHeroes: number[],
    highlightedHeroes: number[],
    addedHeroes?: number[],
    removedHeroes?: number[],
    additionalClasses: string[]
}

interface IState {

}

export class HeroModernGrid extends React.Component<IProps, IState> {
    renderHeroIcons() {
        if (this.props.addedHeroes) {console.log('renderHeroIcons',this.props.addedHeroes,this.props.removedHeroes)}
        const items=[]

        for (const i in HeroData.heroIdsOrderedByName) {
            const heroId=HeroData.heroIdsOrderedByName[i]
            const prob=this.props.heroProb?.get(heroId) || 0
            const additionalClasses=["heroIconSmall"]
            if (this.props.selectedHeroes.indexOf(heroId)>=0) {additionalClasses.push("mySelected")}
            if (this.props.highlightedHeroes.indexOf(heroId)<0) {additionalClasses.push("myNotHighlighted")}
            items.push(
                <HeroModernIcon key={i} additionalClasses={additionalClasses} heroId={heroId} heroProb={prob} onClick={this.props.onClick} isAdded={this.props.addedHeroes?.includes(heroId)} isRemoved={this.props.removedHeroes?.includes(heroId)}/>
            )
        }
        return items
    }

    render() {

        return (
            <Box className={"heroModernGrid " + this.props.additionalClasses.join(" ")}>
                {this.renderHeroIcons()}
            </Box>
        )
    }
}