import React from "react";
import {Box, Chip, Container, Divider, ToggleButton, ToggleButtonGroup} from "@mui/material";
import {HeroCounterPackGrid, HeroCounterHeroGrid, HeroGrid, HeroPackSelected} from "./index";
import {IHeroPack} from "../types/IHeroPack";
import {MyNavigator} from "../types/MyNavigator";
import {MySettings} from "../types/MySettings";
import {HeroData} from "../types";

interface IProps {
    heroIds: IHeroPack,
    currentMode: number
}

interface IState {
    heroIds: IHeroPack,
    heroLostProbs: Map<number, number>,
    heroCounterProbs: Map<number, number>,
    heroAttackerProbs: Map<number, number>,
    heroLostPacks: IHeroPack[],
    heroWinPacks: IHeroPack[],
    heroWinProbs: Map<number,number>
    heroCounterFilter: number[],
    heroAttackerFilter: number[],
    requestsInProgress: number,
    currentMode: number
}

export class HeroCounterPackMode extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            heroIds: props.heroIds,
            heroLostPacks: [],
            heroCounterFilter: [],
            heroWinPacks: [],
            heroAttackerFilter: [],
            requestsInProgress: 0,
            heroLostProbs: new Map<number,number>(),
            heroWinProbs: new Map<number,number>(),
            heroCounterProbs: new Map<number,number>(),
            heroAttackerProbs: new Map<number,number>(),
            currentMode: props.currentMode
        };
        this.requestsInProgress=0
        this.currentMode=props.currentMode
        this.selectHero = this.selectHero.bind(this)
        this.filterCounterHero = this.filterCounterHero.bind(this)
        this.filterAttackerHero = this.filterAttackerHero.bind(this)
        this.selectMode=this.selectMode.bind(this)
        this.updateHeroIds(this.state.heroIds)
        MyNavigator.heroCounterPackMode=this
        //console.log('created heroCounterPackMode',this.state.heroIds)
    }
    currentMode: number
    requestsInProgress: number
    initialized: boolean = false
    requestSeq=0


    getPacksData(heroIds:IHeroPack,currentMode:number) {
        const requestId=this.requestSeq++
        const heroParams=[-1,-1,-1,-1,-1]
        for (let i=0;i<5 && i<heroIds.hIds.length;i++) {heroParams[i]=heroIds.hIds[i];}
        this.requestsInProgress++
        this.setState({requestsInProgress:this.requestsInProgress})
        let url=MySettings.apiUrl
        if (currentMode===0) {
            url+="findLostPackHeroes/"
        } else {
            url+="findWinPackHeroes/"
        }
        fetch(url+heroParams.join("/"), {
            method: "GET"
        }).then(res => res.json())
            .then(
                (result) => {
                    if (result['items']) {
                        const heroLostPacks = []
                        let cnt = 0
                        const heroProbs = new Map<number, number>()
                        for (const x in result['items']) {
                            const z = result['items'][x]
                            let lostPack = []
                            if (currentMode === 0) {
                                lostPack = [~~z.a1, ~~z.a2, ~~z.a3, ~~z.a4, ~~z.a5]
                            } else {
                                lostPack = [~~z.d1, ~~z.d2, ~~z.d3, ~~z.d4, ~~z.d5]
                            }
                            for (const heroId of lostPack) {
                                let m = heroProbs.get(~~heroId)
                                if (m) {
                                    m++
                                } else {
                                    m = 1
                                }
                                heroProbs.set(~~heroId, m)

                            }
                            cnt++
                            //lostPack.push(Number.parseFloat(z.r))
                            //console.log('XXX5',z)
                            heroLostPacks.push({hIds:lostPack,extendedInfo:{statusInfo:"win_rate="+(Number.parseFloat(z.r)*100).toFixed(1)+"% won_fights="+z.w+" total_fights="+z.t }})
                        }
                        for (const heroPackId of Array.from(heroProbs.keys())) {
                            const mmd = heroProbs.get(~~heroPackId)
                            if (mmd && cnt > 0) heroProbs.set(~~heroPackId, 100 * mmd / cnt)
                        }
                        if (currentMode===0) {
                            //console.log('ULP',currentMode,heroLostPacks)
                            this.setState(
                                {heroIds: heroIds, heroCounterProbs: heroProbs, heroLostPacks: heroLostPacks}
                            )
                        } else {
                            //console.log('UWP',currentMode,heroLostPacks)
                            this.setState(
                                {heroIds: heroIds, heroAttackerProbs: heroProbs, heroWinPacks: heroLostPacks}
                            )
                        }
                    }
                }).finally(()=>{
                    this.requestsInProgress--
                    this.setState({requestsInProgress:this.requestsInProgress})
        })
    }

    updateHeroIds(heroIds: IHeroPack) {
        const currentMode=this.currentMode
        this.initialized=true
        const requestId=this.requestSeq++
            this.setState(
                {heroIds: heroIds, heroLostProbs: new Map<number, number>(), heroLostPacks: [], heroWinProbs: new Map<number, number>(), heroWinPacks: [],heroCounterProbs:new Map<number, number>(),heroAttackerProbs:new Map<number,number>()}
            )
        if (heroIds.hIds.length == 5) {
            this.getPacksData(heroIds,0)
            this.getPacksData(heroIds,1)
        } else {
            const heroParams=[-1,-1,-1,-1,-1]
            for (let i=0;i<5 && i<heroIds.hIds.length;i++) {heroParams[i]=heroIds.hIds[i];}
            //console.log('RIP+1',requestId,this.state.requestsInProgress)
            this.requestsInProgress++
            let url=MySettings.apiUrl+"findLostPackHeroesStat/"
            fetch(url+heroParams.join("/"), {
                method: "GET"
            })
                .then(res => res.json())
                .then(
                    (result) => {
                        //console.log("CS-S",requestId,this.currentMode,0)
                            let heroProbs = new Map<number, number>()
                            if (result['items']) {
                                for (const j in result['items']) {
                                    if (result['items'][j]['d']) {
                                        heroProbs.set(~~result['items'][j]['d'], Number.parseFloat(result['items'][j]['prob']))
                                    }
                                }
                            }
                this.setState({
                    heroLostProbs: heroProbs,
                    heroIds: heroIds,
                    heroLostPacks: []
                });
                            //console.log('XXX2 nulled heroLostPacks:',heroIds)
                    },
                    (error) => {
                        console.error("error req",requestId,error)
                            this.setState({
                                heroLostProbs: new Map<number, number>(),
                                heroIds: heroIds,
                                heroLostPacks: []
                            });
                    }
                ).finally(()=>{
                    //console.log('ReqFin0 ',requestId)
                this.requestsInProgress--
                this.setState({requestsInProgress:this.requestsInProgress})
            })
            this.requestsInProgress++
            this.setState({requestsInProgress:this.requestsInProgress})
            url=MySettings.apiUrl+"/findWinPackHeroesStat/"
            fetch(url+heroParams.join("/"), {
                method: "GET"
            })
                .then(res => res.json())
                .then(
                    (result) => {
                        //console.log("CS-S",requestId,this.currentMode,1)
                        let heroProbs = new Map<number, number>()
                        if (result['items']) {
                            for (const j in result['items']) {
                                if (result['items'][j]['d']) {
                                    heroProbs.set(~~result['items'][j]['d'], Number.parseFloat(result['items'][j]['prob']))
                                }
                            }
                        }
                        this.setState({
                            heroWinProbs: heroProbs,
                            heroIds: heroIds,
                            heroWinPacks: []
                        });
                        //console.log('XXX2 nulled win lost packs:',heroIds)
                    },
                    (error) => {
                        console.error("error req",requestId,error)
                        this.setState({
                            heroIds: heroIds,
                            heroWinPacks: [],
                            heroWinProbs: new Map<number, number>()
                        });
                    }
                ).finally(()=>{
                //console.log('ReqFin1 ',requestId)
                this.requestsInProgress--
                this.setState({requestsInProgress:this.requestsInProgress})
            })
        }
    }

    filterCounterHero(heroId: number) {
        if (HeroData.allHeroesIds.includes(heroId)) {
            const heroCounterFilter = this.state.heroCounterFilter
            const index = heroCounterFilter.indexOf(heroId, 0)
            if (index > -1) {
                heroCounterFilter.splice(index, 1);
                this.setState({heroCounterFilter: heroCounterFilter})
            } else {
                heroCounterFilter.push(heroId)
                this.setState({heroCounterFilter: heroCounterFilter})
            }
        }
    }

    filterAttackerHero(heroId: number) {
        if (HeroData.allHeroesIds.includes(heroId)) {
            const heroAttackerFilter = this.state.heroAttackerFilter
            const index = heroAttackerFilter.indexOf(heroId, 0)
            if (index > -1) {
                heroAttackerFilter.splice(index, 1);
            } else {
                heroAttackerFilter.push(heroId)
            }
            this.setState({heroAttackerFilter: heroAttackerFilter})
        }
    }

    selectHero(heroId: number) {
        if (HeroData.allHeroesIds.includes(heroId)) {
            const heroPack = this.state.heroIds
            const index = heroPack.hIds.indexOf(heroId)
            if (index < 0) {
                if (heroPack.hIds.length < 5) {
                    heroPack.hIds.push(heroId)
                    heroPack.extendedInfo = {statusInfo: ""}
                    this.updateHeroIds(heroPack)
                }
            } else {
                heroPack.extendedInfo = {statusInfo: ""}
                heroPack.hIds.splice(index, 1);
                this.updateHeroIds(heroPack)
            }
        }
    }

    selectMode(e:any,value:string) {
        const newMode=~~value
        this.currentMode=newMode
        //console.log('Set mode ',newMode)
        this.setState({currentMode:newMode})
        this.updateHeroIds(this.state.heroIds)
    }

    render() {

        const currentMode=this.state.currentMode
        //console.log('RENDER CPM',currentMode,this.state.heroIds)
            if (this.state.heroIds.hIds.length == 5) {
               /* if (!this.initialized) {
                    this.updateHeroIds(this.state.heroIds)
                }*/
                //console.log('RE',currentMode,currentMode===0?this.state.heroLostPacks:this.state.heroWinPacks,this.state.heroIds)
                return (
                    <Container sx={{
                        bgcolor: 'background.default',
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "flex-start",
                        alignItems: "center",
                    }}>
                        <ToggleButtonGroup
                            color="secondary"
                            value={""+currentMode}
                            exclusive
                            onChange={this.selectMode}
                        >
                            <ToggleButton value="0" color="primary">Кто побеждает твой пак</ToggleButton>
                            <ToggleButton value="1" color="primary">Кого побеждает твой пак</ToggleButton>
                        </ToggleButtonGroup>
                        <Divider><Chip label={this.state.requestsInProgress === 0?"Выбранный пак":"Идет загрузка...Просто жди! ("+this.state.requestsInProgress+")"}/></Divider>
                        <HeroPackSelected heroPack={this.state.heroIds} deselectHero={this.selectHero} key={this.state.heroIds.hIds.join(",")}/>
                        <Divider><Chip label={this.state.requestsInProgress === 0?(currentMode===0?"Кто из героев побеждает твой пак - можешь здесь отфильтровать":"Кто из героев проиграл твоему паку - можешь здесь отфильтровать"):"Идет загрузка...Просто жди!("+this.state.requestsInProgress+")"}/></Divider>
                        <HeroCounterHeroGrid heroProbs={currentMode===0?this.state.heroCounterProbs:this.state.heroAttackerProbs}
                                             onFilterCounterHero={currentMode===0?this.filterCounterHero:this.filterAttackerHero}
                                             heroCounterFilter={currentMode===0?this.state.heroCounterFilter:this.state.heroAttackerFilter} key={10+currentMode}

                        />
                        <Divider><Chip label={this.state.requestsInProgress === 0?(this.state.currentMode===0?"Контр-паки":"Кто проиграл твоему паку"):"Идет загрузка...Просто жди! ("+this.state.requestsInProgress+")"}/></Divider>
                        <HeroCounterPackGrid heroLostPacks={currentMode===0?this.state.heroLostPacks:this.state.heroWinPacks}
                                             onFilterCounterHero={currentMode===0?this.filterCounterHero:this.filterAttackerHero}
                                             heroCounterFilter={currentMode===0?this.state.heroCounterFilter:this.state.heroAttackerFilter} key={20+currentMode}
                                             mainHeroPack={this.state.heroIds} mainHeroPackType={currentMode===0?'d':'a'} emptyMessage={"Не переживай. Этот пак еще не победил в "+(currentMode===0?"защите. Попробуй добавить его в защищающиеся в режиме рулетки, и если он победит, то тут появится результат.":"атаке. Попробуй добавить его в атакующие в режиме рулетки, и если он победит, то тут появится результат.")}
                        />
                    </Container>
                )
            } else {
                //console.log('RE2',currentMode,currentMode===0?this.state.heroLostPacks:this.state.heroWinPacks,this.state.heroIds)
                /*if (!this.initialized) {
                    this.updateHeroIds(this.state.heroIds)
                }*/
                return (

                    <Container sx={{
                        //width: '100%',
                        m: 0.25,
                        p: 0.25,
                        bgcolor: 'background.default',
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "flex-start",
                        alignItems: "center",
                    }}>
                        <ToggleButtonGroup
                            color="secondary"
                            value={""+this.currentMode}
                            exclusive
                            onChange={this.selectMode}
                        >
                            <ToggleButton value="0" color="primary">Кто побеждает твой пак</ToggleButton>
                            <ToggleButton value="1" color="primary">Кого побеждает твой пак</ToggleButton>
                        </ToggleButtonGroup>
                        <HeroPackSelected heroPack={this.state.heroIds} deselectHero={this.selectHero}/>
                        <Divider><Chip label={this.state.requestsInProgress === 0?(this.currentMode===0?"Выбрать защитный пак":"Выбрать атакующий пак"):"Идет загрузка...Просто жди!("+this.state.requestsInProgress+")"}/></Divider>
                        <Box sx={{
                            m: 0,
                            p: 0
                        }}>
                            <HeroGrid showAll={true} heroIds={this.state.heroIds} heroProbs={this.currentMode===0?this.state.heroLostProbs:this.state.heroWinProbs}
                                      selectHero={this.selectHero} key={30+this.currentMode}/>
                        </Box>
                    </Container>
                )
            }
    }
}

export default HeroCounterPackMode;