import React from "react";
import {HeroPackSelected} from "./index";
import {Box, Button, Chip, Divider} from "@mui/material";
import {HeroData} from "../types";
import {IHeroPack} from "../types/IHeroPack";
import {MyNavigator} from "../types/MyNavigator";
import { MySettings } from "../types/MySettings";

interface IQueueItem {
    position: number,
    heroIdsA: IHeroPack,
    heroIdsD: IHeroPack,
    testQId?: number
}

interface IProps {
    queueItems: IQueueItem[]
}

interface IState {
    queueItems: IQueueItem[],
    requestsInProgress: number
}

export class HeroFightQueueMode extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            queueItems: props.queueItems,
            requestsInProgress: 0
        }
        this.refreshQueue=this.refreshQueue.bind(this)
        this.refreshQueue()
        this.removeIcon=this.removeIcon.bind(this)
    }

    removeIcon(e:any) {
        console.log('e',e)
        if (e && e.target && e.target.dataset && e.target.dataset.testqid) {
        fetch(MySettings.apiUrl+"queue/"+e.target.dataset.testqid, {
            method: "DELETE"
            }).finally(()=>{
                this.refreshQueue()
            })
        }
    }

    refreshQueue() {
        const state=this.state; this.setState({requestsInProgress:state.requestsInProgress+1})
        fetch(MySettings.apiUrl+"queue/", {
            method: "GET"
        })
            .then(res => res.json())
            .then(
                (result) => {
                if (result.items) {
                    const newQ:IQueueItem[]=[]
                    let position=0
                    for (const i in result.items) {
                        position++
                        const x=result.items[i]
                        const heroIdsA=[]; for (let j=1;j<6;j++) {if (HeroData.getHeroById(~~x['a'+j])) {heroIdsA.push(~~x['a'+j])}}
                        const heroIdsD=[];for (let j=1;j<6;j++) {if (HeroData.getHeroById(~~x['d'+j])) {heroIdsD.push(~~x['d'+j])}}
                        newQ.push({position:position,heroIdsA:{hIds:heroIdsA},heroIdsD:{hIds:heroIdsD},testQId:x['testq_id']})
                    }
                    this.setState({queueItems:newQ})
                }
                }).finally(()=>{
            const state=this.state; this.setState({requestsInProgress:state.requestsInProgress-1})
        })
    }

    renderQueue() {
        const items=[]
        this.state.queueItems.sort((a:any,b:any)=> {
            return a.position-b.position
        })
        for (const i in this.state.queueItems) {
            const x=this.state.queueItems[i]
            items.push(<Box sx={{
                bgcolor: 'primary.main',
                color: 'primary.contrastText',
                marginBottom: '3px',
                borderRadius: '10px',
            }}key={i}>
                <Box><HeroPackSelected heroPack={x.heroIdsA} deselectHero={()=>{}}/>
                </Box>
                <Box className={"heroQueueTwoVSBox"}><span className={"heroQueueTwoVSPosition"}>{x.position}</span><span className={"heroQueueTwoVSVS"}>VS</span></Box>
                <Box><HeroPackSelected heroPack={x.heroIdsD} deselectHero={()=>{}}/></Box>
                <Button className={"closeButton"} sx={{
                    display: MyNavigator.isCloseDisabled()?'none':'inline-flex'
                }} hidden={MyNavigator.isCloseDisabled()} data-testqid={x.testQId}><img data-testqid={x.testQId} onClick={this.removeIcon} className={"closeIcon"} alt="Close"  src={"close_icon.png"}/></Button>
            </Box>)

        }
        return items
    }

    render() {
        return (
            <Box>
            <Divider><Chip label={this.state.requestsInProgress>0?"Идет загрузка...Просто жди!":"Текущая очередь атаки. Нажмите сюда для обновления"} onClick={this.refreshQueue}/></Divider>
            <Box className={"heroQueueTwoBox"}>{this.renderQueue()}</Box>
            </Box>
        )
    }
}

export default HeroFightQueueMode