import React from "react";
import {Box} from "@mui/material";
import {HeroData} from "../types";
import AddCircleOutlineRoundedIcon from '@mui/icons-material/AddCircleOutlineRounded';
import DoDisturbOnOutlinedIcon from '@mui/icons-material/DoDisturbOnOutlined';

interface IProps {
    heroId: number,
    heroProb: number,
    onClick: any,
    additionalClasses: string[],
    isAdded?: boolean,
    isRemoved?: boolean,
}

interface IState {

}

export class HeroModernIcon extends React.Component<IProps, IState> {
    constructor(props:any) {
        super(props);
        this.onClick=this.onClick.bind(this)
    }
    onClick(e:any) {
        if (e?.target?.dataset?.heroid > 0) {
            this.props.onClick(~~e.target.dataset.heroid)
        }
    }

    render() {
        let hero = HeroData.getHeroById(this.props.heroId)
        //hero = HeroData.getHeroById(19);
        const heroProb=this.props.heroProb||0
        if (!hero) {
            hero = HeroData.emptyHero
        }
        let boxClassName = "heroModernIcon "+this.props.additionalClasses.join(" ")
        return (
            <Box className={boxClassName} data-heroid={hero.heroId} onClick={this.onClick} >
                {this.props.isAdded !==undefined && this.props.isAdded && (<span className={"someHeroIcon"}><AddCircleOutlineRoundedIcon/></span>)}
                {this.props.isRemoved !==undefined && this.props.isRemoved && (<span className={"someHeroIcon"}><DoDisturbOnOutlinedIcon/></span>)}
                <span className={"heroModernIconName"}>{/*hero.heroId+" "+*/ hero.heroName}</span>
                <span className={"heroModernIconProb"}>{heroProb > 0 ? heroProb.toFixed(2) + "%" : ""}</span>
                <img  alt={""}
                     src={"hero/" + HeroData.padLeadingZeros(hero.heroId, 4) + ".png"}/>

            </Box>
        )
    }
}