import React from "react";
import {HeroIcon} from "./index";
import {Box, Grid} from "@mui/material";
import {HeroData} from "../types";
import {HeroModernIcon} from "../components2/HeroModernIcon";

interface IProps {
    onFilterCounterHero: any,
    heroProbs: Map<number, number>
    heroCounterFilter: number[]
}

interface IState {

}

export class HeroCounterHeroGrid extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {};
    }

    renderHeroes() {
        const items = []
        //if (this.props.heroProbs) {
            const heroArray:any[]=[]
            for (const x of Array.from(this.props.heroProbs.keys())) {
                const hero = HeroData.getHeroById(~~x)
                const prob = this.props.heroProbs.get(~~x) || -1
                if (hero && prob) {
                    heroArray.push({hero:hero,prob:prob,heroName:hero.heroName})
                }
            }
            heroArray.sort((a,b)=>{
                return a.heroName>b.heroName?1:-1
            })
            for (const x of heroArray) {
               /* items.push(<HeroIcon heroId={x.hero.heroId} renderType={10}
                                         isSelected={this.props.heroCounterFilter.indexOf(x.hero.heroId) > -1}
                                         OnClick={this.props.onFilterCounterHero} heroProb={x.prob} key={x.hero.heroId}/>)*/
                const additionalClasses=["heroIconSmall"]
                if (this.props.heroCounterFilter.indexOf(x.hero.heroId) > -1) {additionalClasses.push("mySelected")}
                items.push(<HeroModernIcon heroId={x.hero.heroId}
                                           additionalClasses={additionalClasses}
                                     onClick={this.props.onFilterCounterHero} heroProb={x.prob} key={x.hero.heroId}/>)
            }
       // }
        return items
    }

    render() {
        return (<Box className={"heroCounterHeroGrid"} sx={{

            bgcolor: 'primary.light'
        }}>
            {this.renderHeroes()}
        </Box>)
    }
}

export default HeroCounterHeroGrid