import React from "react";
import {HeroPackSelected} from "./index";
import {Box, Chip, Divider} from "@mui/material";
import {HeroData} from "../types";
import {IHeroPack} from "../types/IHeroPack";
import {MyNavigator} from "../types/MyNavigator";
import {MySettings} from "../types/MySettings";

interface IQueueItem {
    position: number,
    heroIdsA: IHeroPack,
    testQId?: number
}

interface IProps {
    queueItems: IQueueItem[]
}

interface IState {
    queueItems: IQueueItem[],
    requestsInProgress: number
}

export class HeroRouletteAttackersQueueMode extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            queueItems: props.queueItems,
            requestsInProgress: 0
        }
        this.refreshQueue=this.refreshQueue.bind(this)
        this.refreshQueue()
        this.removeIcon=this.removeIcon.bind(this)
        MyNavigator.heroRouletteAttackersQueueMode=this
    }

    removeIcon(e:any) {
        console.log('e',e)
        if (e && e.target && e.target.dataset && e.target.dataset.testqid) {
        fetch(MySettings.apiUrl+"queueRA/"+e.target.dataset.testqid, {
            method: "DELETE"
            }).finally(()=>{
                this.refreshQueue()
            })
        }
    }

    refreshQueue() {
        const state=this.state; this.setState({requestsInProgress:state.requestsInProgress+1})
        fetch(MySettings.apiUrl+"queueRA/", {
            method: "GET"
        })
            .then(res => res.json())
            .then(
                (result) => {
                if (result.items) {
                    const newQ:IQueueItem[]=[]
                    let position=0
                    for (const i in result.items) {
                        position++
                        const x=result.items[i]
                        const heroIdsA=[]; for (let j=1;j<6;j++) {if (HeroData.getHeroById(~~x['a'+j])) {heroIdsA.push(~~x['a'+j])}}
                        newQ.push({position:position,heroIdsA:{hIds:heroIdsA},testQId:x['testq_id']})
                    }
                    this.setState({queueItems:newQ})
                }
                }).finally(()=>{
            const state=this.state; this.setState({requestsInProgress:state.requestsInProgress-1})
        })
    }

    renderQueue() {
        const items=[]
        this.state.queueItems.sort((a:any,b:any)=> {
            return a.position-b.position
        })
        for (const i in this.state.queueItems) {
            const x=this.state.queueItems[i]
            items.push(<Box className={"heroBoxQueueWithNum"} sx={{
                display: 'flex',
                bgcolor: 'primary.main',
                color: 'primary.contrastText',
                p: '0.5em 0.5em 0.5em 0.5em',
                marginBottom: '0.1em',
                borderRadius: '10px',
            }} key={i}>
                <Box sx={{
                    fontSize: '1em',
                    fontWeight: 700,
                    marginLeft: '0.1em',
                    marginRight: '0.5em'
                }}>{x.position}</Box>
                <Box><HeroPackSelected heroPack={x.heroIdsA} deselectHero={()=>{}}/>
            </Box>
                <Box hidden={MyNavigator.isCloseDisabled()}><img data-testqid={x.testQId} className={"closeIcon"} onClick={this.removeIcon} src={"close_icon.png"}/></Box>
            </Box>)
        }
        return items
    }

    render() {
        return (
            <Box>
            <Divider><Chip label={this.state.requestsInProgress>0?"Идет загрузка...Просто жди!":"Текущая очередь атаки. Нажмите сюда для обновления"} onClick={this.refreshQueue}/></Divider>
            <Box>{this.renderQueue()}</Box>
            </Box>
        )
    }
}

export default HeroRouletteAttackersQueueMode