import React from 'react';
import './App.css';

import { ThemeProvider } from '@mui/system';
import {HeroModeSelect} from "./components";
import {createTheme} from "@mui/material";
import {MySettings} from "./types/MySettings";
import {MainScreen} from "./components2/MainScreen";

const customTheme = createTheme({
    palette: {
        primary: {
            main: '#5d4037',
            contrastText: '#fff',
            light: '#8b6b61',
            dark: '#321911',
        },
        secondary: {
            light: '#ffc947',
            main: '#ff9800',
            dark: '#c66900',
            contrastText: '#000',
        }
    }
});

function App() {
    if (MySettings.mainVersion===1) {
        return (
            <ThemeProvider theme={customTheme}>
                <HeroModeSelect currentMode={4}/>
            </ThemeProvider>
        );
    } else {
        return (
        <ThemeProvider theme={customTheme}>
            <MainScreen/>
        </ThemeProvider>
        )
    }
}

export default App;
