import {Box, Slider} from "@mui/material";
import React, {useState} from "react";


export function ModernSlider(props: any) {
    const [minFights, setMinFights] = useState(props.initial)
    const marks=[{
        value: props.min,
        label: ""+props.min
    }, {
        value: props.max,
        label: ""+props.max
    }]
    const changeValue = function (e: any, value: number | number []) {
        if (!Array.isArray(value)) {
            setMinFights(value)
            props.callback(value)
        }
    }
    const getSliderText = function (value: number) {
        return "" + value
    }

    return (
        <Box className={"modernSlider"}>
        <Slider
            getAriaLabel={() => props.label}
            value={minFights}
            min={props.min}
            max={props.max}
            marks={marks}
            onChange={changeValue}
            getAriaValueText={getSliderText}
            color={"secondary"}
            track="inverted"
            step={1}
            valueLabelDisplay="on"
            disableSwap
        />
        </Box>
    )

}