import {default as React} from 'react'
import {Box, Grid} from "@mui/material";
import {Hero, HeroData} from "../types";

//const { t, i18n } = useTranslation("ns1")

function padLeadingZeros(num: number, size: number) {
    let s = num + "";
    while (s.length < size) s = "0" + s;
    return s;
}

interface IProps {
    heroId?: number,
    isSelected: boolean,
    OnClick?: any,
    heroProb: number,
    renderType: number
}

interface IState {
    heroId?: number,
    isSelected: boolean
}

class HeroIcon extends React.Component<IProps, IState> {

    constructor(props: any) {
        super(props);
        this.state={
            heroId: props.heroId,
            isSelected: props.isSelected
        }
    }

    renderForHeroSelectGrid() {
        let hero: Hero | undefined
        if (this.props.heroId)
            hero = HeroData.getHeroById(this.props.heroId)
        if (hero) {
            //const url = "url('hero/" + padLeadingZeros(hero.heroId, 4) + ".png')"
            let prob = null
            if (this.props.heroProb && this.props.heroProb>=0.0) {
                let heroProb = this.props.heroProb
                prob=<span className={"heroProb"}>{heroProb.toFixed(2)}%</span>
            }
            return (<Grid item xs={1} md={1} className={"heroIconGrid"} sx={{
                bgcolor: this.props.isSelected?'primary.light':'primary.main',
                color: 'primary.contrastText',
                m: 0.25,
                textAlign: 'center',
                fontSize: '0.6em',
                fontWeight: '700',
                flexBasis: '0 !important',
                flexGrow: 0,
                maxWidth: 'max-content',
            }} key={hero.heroId}>
                <Box sx={{
                    alignItems: 'flex-end',
                    display: 'flex',
                    flexWrap: 'wrap',
                    p: '0px 0px 0px 0px !important',
                    flexBasis: 0,
                }}  >

                    <img data-heroid={hero.heroId} onClick={this.props.OnClick} className={"heroIcon"} alt={""} src={"hero/" + padLeadingZeros(hero.heroId, 4) + ".png"}/>
                    {prob}
                    <span className={"heroIconSpan"}>{hero.heroName+ " ("+hero.heroId+")"}</span>
                </Box>
            </Grid>)
        }else {
            console.error('HIC render',this.props.renderType,this.props.heroId)
            return this.renderForPackEmptySelected()
        }
    }

    renderForHeroCounterHeroGrid() {
        let hero: Hero | undefined
        if (this.props.heroId)
            hero = HeroData.getHeroById(this.props.heroId)
        if (hero) {
            //const url = "url('hero/" + padLeadingZeros(hero.heroId, 4) + ".png')"
            let prob = null
            if (this.props.heroProb && this.props.heroProb>=0.0) {
                let heroProb = this.props.heroProb
                prob=<span className={"heroProb"}>{heroProb.toFixed(2)}%</span>
            }
            return (<Grid item xs={1} className={"heroIconGrid"} sx={{
                bgcolor: 'primary.main',
                opacity: this.props.isSelected?'60%':'100%',
                color: 'primary.contrastText',
                m: 0.25,
                textAlign: 'center',
                fontSize: '0.6em',
                fontWeight: '700',
                flexBasis: '0px !important',
                maxWidth: 'max-content',
                flexGrow: 0,
            }} key={hero.heroId}>
                <Box sx={{
                    alignItems: 'flex-end',
                    display: 'flex',
                    flexWrap: 'wrap',
                    p: '0px 0px 0px 0px !important',
                    flexBasis: '0px !important',
                }}  >

                    <img data-heroid={hero.heroId} onClick={this.props.OnClick} className={"heroIcon"} alt={""} src={"hero/" + padLeadingZeros(hero.heroId, 4) + ".png"}/>
                    {prob}
                    <span className={"heroIconSpan"}>{hero.heroName + " ("+hero.heroId+")"}</span>
                </Box>
            </Grid>)
        } else {
            console.error('HIC render',this.props.renderType,this.props.heroId)
            return this.renderForPackEmptySelected()
        }
    }



    renderForHeroPackGrid() {
        let hero: Hero | undefined
        if (this.props.heroId)
            hero = HeroData.getHeroById(this.props.heroId)
        if (hero) {
            //const url = "url('hero/" + padLeadingZeros(hero.heroId, 4) + ".png')"
            let prob = null
            if (this.props.heroProb && this.props.heroProb>=0.0) {
                let heroProb = this.props.heroProb
                prob=<span className={"heroProb"}>{heroProb.toFixed(2)}%</span>
            }
            return (<Grid item  className={"heroIconGrid"} sx={{
                bgcolor: this.props.isSelected?'primary.light':'primary.main',
                color: 'primary.contrastText',
                m: '0% 0% 0% 0%',
                borderRadius: 2,
                textAlign: 'center',
                fontSize: '0.6em',
                fontWeight: '700',
                maxWidth: 'max-content'
            }} key={hero.heroId}>
                <Box sx={{
                    maxWidth: '7.1em',
                    alignItems: 'flex-end',
                    display: 'flex',
                    flexWrap: 'wrap',
                    p: '0px 0px 0px 0px !important',
                    flexBasis: 0,
                }}  >

                    <img data-heroid={hero.heroId} onClick={this.props.OnClick} className={"heroIcon"} alt={""} src={"hero/" + padLeadingZeros(hero.heroId, 4) + ".png"}/>
                    {prob}
                    <span className={"heroIconSpan"}>{hero.heroName+ " ("+hero.heroId+")"}</span>
                </Box>
            </Grid>)
        } else {
            console.error('HIC render',this.props.renderType,this.props.heroId)
            return this.renderForPackEmptySelected()
        }
    }

    render() {
        switch(this.props.renderType) {
            case 0: return this.renderForHeroSelectGrid()
            case 1: return this.renderForHeroSelectGrid()
            case 2: return this.renderForPackEmptySelected()
            case 10: return this.renderForHeroCounterHeroGrid()
            case 15: return this.renderForHeroPackGrid()
            case 20: return this.renderForHeroSelectGrid()
        }
        this.renderForPackEmptySelected()
    }

    renderForPackEmptySelected() {
            return <Box className={"heroIconGridEmpty"} sx={{
                /*maxWidth: '7.1em',
                minWidth: '7.1em',
                minHeight: '8.45em',
                alignItems: 'flex-end',
                display: 'flex',*/
                bgcolor: 'primary.main',
                color: 'primary.contrastText',
               /* fontSize: '0.6em',
                fontWeight: '700',
                textAlign: 'center',
                flexBasis: '0px !important',
                flexGrow: 0,
                m: 0.0,
                borderRadius: 2,*/
            }}>
                <span className={"heroIconSpan"}>пусто</span>
            </Box>
        }
}

export default HeroIcon