import React from "react";
import {HeroGrid, HeroPackSelected} from "./index";
import {Box, Button, Chip, Container, Divider} from "@mui/material";
import {IHeroPack} from "../types/IHeroPack";
import {MyNavigator} from "../types/MyNavigator";
import {MySettings} from "../types/MySettings";
import {HeroData} from "../types";


interface IProps {
    heroIdsA: IHeroPack,
    navigateFunction: any
}

interface IState {
    heroIdsA: IHeroPack,
    displayEditA: boolean,
    requestsInProgress: number
}

export class HeroAddAttackerMode extends React.Component<IProps, IState> {
    static emptyMap=new Map<number,number>()
    constructor(props:any) {
        super(props);
        this.state = {
            heroIdsA: props.heroIdsA,
            displayEditA: props.heroIdsA.hIds.length!==5,
            requestsInProgress: 0
        };
        this.selectHeroA=this.selectHeroA.bind(this)
        this.toggleEditA=this.toggleEditA.bind(this)
        this.sendToQueue=this.sendToQueue.bind(this)
        this.setHeroPack=this.setHeroPack.bind(this)
        MyNavigator.heroAddAttackerMode=this
    }

    setHeroPack(heroPack:IHeroPack) {
        this.setState({heroIdsA:heroPack,displayEditA:heroPack.hIds.length!==5})
    }

    sendToQueue() {
        const body:any={
            a1:-1,
            a2:-1,
            a3:-1,
            a4:-1,
            a5:-1,
        }
        for (let i=0;i<5 && i<this.state.heroIdsA.hIds.length;i++) {body["a"+(i+1)]=this.state.heroIdsA.hIds[i];}
        const state=this.state; this.setState({requestsInProgress:state.requestsInProgress+1})
        fetch(MySettings.apiUrl+"queueRA/",{
            method: "POST",
            headers: {
                "Content-type": "application/json"
            },
            body: JSON.stringify(body)}).then(()=>{
                console.log("Good")
                this.props.navigateFunction(1)
        }).finally(()=>{
            const state=this.state; this.setState({requestsInProgress:state.requestsInProgress-1})
        })
    }

    selectHeroA(heroId: number) {
        if (HeroData.allHeroesIds.includes(heroId)) {
            const heroPack = this.state.heroIdsA
            const index = heroPack.hIds.indexOf(heroId)
            if (index < 0) {
                if (heroPack.hIds.length < 5) {
                    heroPack.hIds.push(heroId)
                    heroPack.extendedInfo = {statusInfo: ""}
                    this.setState({heroIdsA:heroPack })
                }
            } else {
                heroPack.extendedInfo = {statusInfo: ""}
                heroPack.hIds.splice(index, 1);
                this.setState({heroIdsA:heroPack })
            }
        }
    }


    toggleEditA() {
        const newState=!this.state.displayEditA
        this.setState({displayEditA:newState})
    }

    renderA() {
        if (this.state.displayEditA) {
            return (<Box className={"heroADBox"}><Divider><Chip label={this.state.requestsInProgress>0?"Идет загрузка...просто жди!":"Атакующий пак"} /></Divider>
                <HeroPackSelected heroPack={this.state.heroIdsA} deselectHero={this.selectHeroA}/>
                {this.state.heroIdsA.hIds.length>0?
                    <Button variant="outlined"  className={"heroEditPackButton"} onClick={this.toggleEditA}>Готово</Button>
                    :
                    null}
                <Divider><Chip
                    label={this.state.requestsInProgress > 0 ? "Идет загрузка...просто жди!" : "Выбери атакующих героев"}/></Divider>
                <HeroGrid showAll={false} heroIds={this.state.heroIdsA} heroProbs={HeroAddAttackerMode.emptyMap}
                          selectHero={this.selectHeroA}/>
            </Box>)
        } else {
            return (<Box className={"heroADBox"}><Divider><Chip label={this.state.requestsInProgress>0?"Идет загрузка...просто жди!":"Атакующий пак"} /></Divider>

                <HeroPackSelected heroPack={this.state.heroIdsA} deselectHero={this.selectHeroA}/>
                <Button variant="outlined"  className={"heroEditPackButton"} onClick={this.toggleEditA}>Изменить</Button>
            </Box>)
        }
    }


    renderSendButton() {
        if (this.state.heroIdsA.hIds.length>0 && !this.state.displayEditA) {
            return (<Button variant="contained" onClick={this.sendToQueue} sx={{
                marginTop: '1em',
                maxWidth: 'min-content'
            }}>Отправить</Button>)
        } else {
            return (<Button disabled>Выбери героев перед отправкой</Button>)
        }
    }

    render() {
        return (<Box sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center'
        }}>
            {this.renderA()}
            {this.renderSendButton()}
        </Box>)
    }
}

export default HeroAddAttackerMode;