import React from "react";
import {HeroFightQueueMode} from "./heroFightQueueMode";
import {Box, Divider, ToggleButton, ToggleButtonGroup} from "@mui/material";
import HeroAddToTrainMode from "./heroAddToTrainMode";
import HeroAddAttackerMode from "./heroAddAttackerMode";
import HeroAddDefenderMode from "./heroAddDefenderMode";
import HeroRouletteAttackersQueueMode from "./heroRouletteAttackersQueueMode";
import HeroRouletteDefendersMode from "./heroRouletteDefendersMode";
import {MyNavigator} from "../types/MyNavigator";

interface IProps {
    currentMode: number,
    navigateFunction: any
}

interface IState {
    currentMode: number,
}

export class HeroRouletteMode extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            currentMode: props.currentMode
        }
        this.setMode=this.setMode.bind(this)
        this.navigateToMode=this.navigateToMode.bind(this)
        MyNavigator.heroRouletteMode=this
    }
    navigateToMode(newMode:number) {
        this.setState({currentMode: newMode})
    }
    setMode(e:any) {
        if (e && e.target && e.target.dataset && e.target.dataset.mode) {
            this.setState({currentMode: ~~e.target.dataset.mode})
        }
    }
    renderMode(currentMode:number) {
        if (currentMode===0) {
            return (<HeroAddAttackerMode heroIdsA={MyNavigator.currentPack} navigateFunction={this.navigateToMode}/>)
        } else if (currentMode===1) {
            return (<HeroRouletteAttackersQueueMode queueItems={[]}/>)
        } else if (currentMode===2) {
            return (<HeroAddDefenderMode heroIdsA={MyNavigator.currentPack} navigateFunction={this.navigateToMode}/>)
        } else if (currentMode===3) {
            return (<HeroRouletteDefendersMode queueItemsUser={[]} queueItemsTopA={[]} queueItemsTopD={[]} queueItemsClassic={[]}/>)
        } else {
            return (null)
        }
    }
    render() {
        return (
            <Box sx={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "flex-start",
                alignItems: "center",
            }}>
                <ToggleButtonGroup
                    color="secondary"
                    value={""+this.state.currentMode}
                    exclusive
                    onChange={this.setMode}
                    className={"heroMainMenuButtons"}
                >
                    <ToggleButton value="0" data-mode={0} color="primary">Добавить в атакующие паки</ToggleButton>
                    <ToggleButton value="1" data-mode={1} color="primary">Список атакующих паков</ToggleButton>
                    <ToggleButton value="2" data-mode={2} color="primary">Добавить в защищающиеся паки</ToggleButton>
                    <ToggleButton value="3" data-mode={3} color="primary">Список защищающихся</ToggleButton>
                </ToggleButtonGroup>
                <Divider />
                {this.renderMode(this.state.currentMode)}
            </Box>
        )
    }
}

export default HeroRouletteMode