import React from "react";
import {Box, Button, Grid, Menu, MenuItem, Stack, Table, TableBody, Tooltip} from "@mui/material";
import {HeroIcon} from "./index";
import {HeroData} from "../types";
import {IHeroPack} from "../types/IHeroPack";
import {MyNavigator} from "../types/MyNavigator";
import {HeroModernIcon} from "../components2/HeroModernIcon";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import LinkIcon from "@mui/icons-material/Link";

interface IProps {
    heroPack: IHeroPack,
    heroIdsA?: IHeroPack,
    heroIdsD?: IHeroPack,
    deselectHero: any
}

interface IState {
    //heroIds: IHeroPack,
    anchorEl: any,
}

export class HeroPackSelected extends React.Component<IProps, IState> {
    constructor(props:any) {
        super(props);
        this.state = {
            //heroIds: props.heroIds,
            anchorEl: null,
        };
        this.addToAttackQueue=this.addToAttackQueue.bind(this)
        this.addToDefenderQueue=this.addToDefenderQueue.bind(this)
        this.seeCounterPacks=this.seeCounterPacks.bind(this)
        this.handleMenuClick=this.handleMenuClick.bind(this)
        this.handleMenuClose=this.handleMenuClose.bind(this)
        this.seeTrainMode=this.seeTrainMode.bind(this)
        this.seeTrainModeA=this.seeTrainModeA.bind(this)
        this.seeTrainModeD=this.seeTrainModeD.bind(this)
    }
    renderHeroPack() {
        const heroCount = this.props.heroPack.hIds.length
        const heroes = this.props.heroPack.hIds
        heroes.sort((a,b) => {
            const h1=HeroData.getHeroById(~~a)
            const h2=HeroData.getHeroById(~~b)
            if (h1 && h2) {
                return h1.battleOrder-h2.battleOrder
            } else {
                return 0
            }
        })
        const result=[]
        for (let i=0;i<5;i++) {
                //result.push((<HeroIcon heroId={heroes[i]} renderType={15} isSelected={false} OnClick={this.props.deselectHero} heroProb={-1} key={i}/>))
                result.push((<HeroModernIcon heroId={heroes[i]} additionalClasses={[]} onClick={this.props.deselectHero} heroProb={-1} key={i}/>))
        }
        return (result)
    }

    addToAttackQueue(e:any) {
        MyNavigator.navigate("RouletteAttackQueue",this.props.heroPack)
    }
    addToDefenderQueue(e:any) {
        MyNavigator.navigate("RouletteDefenderQueue",this.props.heroPack)
    }

    seeCounterPacks(e:any) {
        MyNavigator.navigate("CounterPack",this.props.heroPack)
    }

    seeTrainMode(e:any) {
        MyNavigator.navigate("OneFightPack",this.props.heroIdsA,this.props.heroIdsD)
    }
    seeTrainModeA(e:any) {
        MyNavigator.navigate("OneFightPack",this.props.heroPack,undefined)
    }
    seeTrainModeD(e:any) {
        MyNavigator.navigate("OneFightPack", undefined,this.props.heroIdsD)
    }

    handleMenuClick(e:any) {
        this.setState({anchorEl:e.currentTarget})
    }
    handleMenuClose(e:any) {
        this.setState({anchorEl:null})
    }
    renderMenuItems() {
        const menuItems:JSX.Element[]=[]
        let key=0
        menuItems.push(<MenuItem key={key++} onClick={this.seeCounterPacks}>Посмотреть кто побеждает</MenuItem>)
        if (!HeroData.isPackHasBoss(this.props.heroPack.hIds)) {
            menuItems.push(<MenuItem key={key++} onClick={this.addToAttackQueue}>Добавить в атакующие в
                рулетку</MenuItem>)
            menuItems.push(<MenuItem key={key++} onClick={this.addToDefenderQueue}>Добавить в защищающиеся в
                рулетку</MenuItem>)
            menuItems.push(<MenuItem key={key++} onClick={this.seeTrainModeA}>Добавить в атакующие в Одну
                битву</MenuItem>)
            menuItems.push(<MenuItem key={key++} onClick={this.seeTrainModeD}>Добавить в защищающиеся в Одну
                битву</MenuItem>)
            if (this.props.heroIdsA !== undefined && this.props.heroIdsD !== undefined) menuItems.push(<MenuItem
                key={key++} onClick={this.seeTrainMode}>Add to Train both packs</MenuItem>)
        }
        return menuItems
    }

    render() {
        const additionalClasses = "selectedHeroPack "
        return (
            <Box className={additionalClasses} sx={{
                bgcolor: 'primary.light'
            }}>
                <Box className={"selectedHeroPackIcons"}>
            {this.renderHeroPack()}
                    <Box className={"linkVerticalBar"}>
                        <Box id="demo-positioned-button"
                             aria-controls="demo-positioned-menu"
                             aria-haspopup="true"
                             aria-expanded={this.state.anchorEl ? 'true' : undefined}
                             onClick={this.handleMenuClick}><Tooltip title={"Дополнительная информация"}><MoreVertIcon/></Tooltip></Box>
                    </Box>
                </Box>
                <Menu
                    id="demo-positioned-menu"
                    aria-labelledby="demo-positioned-button"
                    anchorEl={this.state.anchorEl}
                    open={!!this.state.anchorEl}
                    onClose={this.handleMenuClose}
                    anchorOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}
                    transformOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}
                >
                    {this.renderMenuItems()}
                </Menu>

                <Box className={"selectedHeroPackExtended"}>
                    <span>Всего:</span>
                    <Tooltip title={"Всего % побед в атаке"}><span><img
                        src={"./trophy_a_w.png"}/><span>{(this.props.heroPack?.extendedInfo?.wra === undefined ? "?" : this.props.heroPack?.extendedInfo?.wra) + "%"}</span></span></Tooltip>
                    <Tooltip title={"Всего побед в атаке"}><span><img
                        src={"./attack_w.png"}/><span>{this.props.heroPack?.extendedInfo?.ca === undefined ? "?" : this.props.heroPack?.extendedInfo?.ca}</span></span></Tooltip>
                    <Tooltip title={"Всего % побед в защите"}><span><img
                        src={"./trophy_d_w.png"}/><span>{(this.props.heroPack?.extendedInfo?.wrd === undefined ? "?" : this.props.heroPack?.extendedInfo?.wrd) + "%"}</span></span></Tooltip>
                    <Tooltip title={"Всего побед в защите"}><span><img
                        src={"./shield_w.png"}/><span>{this.props.heroPack?.extendedInfo?.cd === undefined ? "?" : this.props.heroPack?.extendedInfo?.cd}</span></span></Tooltip>
                </Box>
                <span className={"heroPackSelectedExtStatus"}>{this.props.heroPack.extendedInfo?.statusInfo?this.props.heroPack.extendedInfo.statusInfo:""}</span>

            </Box>
        )
    }
}

/*
<Box
                    id="demo-positioned-button"
                    aria-controls="demo-positioned-menu"
                    aria-haspopup="true"
                    aria-expanded={this.state.anchorEl ? 'true' : undefined}
                    onClick={this.handleMenuClick}
                    className={"heroButtonThreeDots"}
                >
                    &#10247;
                </Box>
 */
export default HeroPackSelected